<template>
	<div class="operation-product" @click.stop="pClick">
		<btn :title="'-'" class="btnleft" @btnClick="cut" v-show="isShow"/>
		<span v-show="isShow" class="show-num">{{num}}</span>
		<btn :title="'+'" class="btnright" @btnClick="add"/>
	</div>
</template>

<script>
import Btn from './Btn'

export default {
  name: 'OperationProduct',
  components:{
	Btn
  },
  props:{
	productObj:{
		type:Object,
		default(){
			return{}
		}
	}
  },
  data(){
	return{
		num:0
	}
  },
  methods:{
	add(){
		this.num=this.productObj.count
		this.num++
		//使用vuex管理添加
		this.$store.dispatch("addOrderProduct",JSON.parse(JSON.stringify(this.productObj))).then(res=>{
			this.$toast.show(res,3000)
		})
	},
	cut(){
		this.num=this.productObj.count
		this.num--
		//显示数字++
		this.$store.dispatch("cutOrderProduct",this.productObj).then(res=>{
			this.$toast.show(res,3000)
		})
		//减少
		
		//console.log(this.$store.getters.getOrderProductLength)
	},
	pClick(){
	
	}
  },
  //解决数据不及时更新https://www.cnblogs.com/li320212035/p/12893275.html
  computed:{
	isShow(){
		return this.productObj.count
	},
	count(){
		return this.num
	}
	//数量
  },
  mounted(){
	//暂时有问题
	this.num=this.productObj.count
  }

}
</script>

<style>

.operation-product{
	text-align:right;
}
.btnleft{
	margin-right:25px;
	background:#ccc;
}
.show-num{
	font-size:18px;
}
.btnright{
	margin-left:25px;
}
</style>
